.tool-tip:hover span {
  opacity: 1;
  filter: alpha(opacity=100);
  top: -4.5em;
  left: -200px;
  z-index: 100000;
  width: 240px;
  transition: all 0.2s ease;
}

.tool-tip span:hover {
  left: 1000px;
  opacity: 0;
  width: 0;
}
.tool-tip span {
  background: none repeat scroll 0 0 #222; /*-- some basic styling */
  color: #e2e2e1;
  font-family: "Helvetica";
  font-size: 0.8em;
  font-weight: normal;
  line-height: 1.5em;
  padding: 10px;
  /* width: 240px; */
  width: 0;
  top: -4em; /*-- this is the original position of the tool-tip when it's hidden */
  left: 1000px;
  margin-left: 0;
  /*-- set opacity to 0 otherwise our animations won't work */
  opacity: 0;
  filter: alpha(opacity=0);
  position: absolute;
  text-align: center;
  z-index: 1000;
  text-transform: none;
  transition: all 0.3s ease-in-out;
  border-radius: 10px;
}

.tool-tip span:after {
  border-color: #222 rgba(0, 0, 0, 0);
  border-style: solid;
  border-width: 10px 10px 0;
  bottom: -15px;
  content: "";
  display: block;
  left: 195px;
  position: absolute;
  width: 0;
}

