.SlideIn-appear {
  transform: translateX(-100px);
  opacity: 0;
}

/* transform: scale(0);
opacity: 0; */

/* transform: scale(1);
opacity: 1; */

.SlideIn-appear.SlideIn-appear-active {
  opacity: 1;
  transform: translateX(0);
  transition: all 0.2s linear;
}
.SlideIn-enter {
  opacity: 0;
  transform: translateX(-100px);
}
.SlideIn-enter.SlideIn-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: all 0.2s linear;
}
.SlideIn-leave {
  opacity: 1;
  transform: translateX(0);
}
.SlideIn-leave.SlideIn-leave-active {
  opacity: 0;
  position: absolute;
  width: 100%;
  transform: translateX(-100px);
  transition: all 0.2s linear;
}

