.my-card {
  padding: 50px;
  border-radius: 10px;
  text-align: left;
  /* box-shadow: 0 0 10px 0 inset grey; */
  background-color: rgba(255, 255, 255);
  box-shadow: 9px 9px 16px rgb(163, 177, 198, 0.6),
    -9px -9px 16px rgba(255, 255, 255, 0.5);
}

@media (max-width: 600px) {
  .my-card {
    padding: 20px;
  }
}

@media (max-width: 600px) {
  .card-title-responsive {
    font-size: 20px;
  }
}
